 /* thm button start */
 .thm-btn{
	width: fit-content;
	height: fit-content;
	padding: 15px 30px;
	font-size: 14px;
	text-transform: uppercase;
	color: white;
	font-weight: 600;
	background: var(--primry-clr);
	gap: 10px;
	display: inline-block;
	position: relative;
	overflow: hidden;
	transition: all 400ms ease-in-out;
  }
  .thm-btn i{
	margin-left: 8px;
  }
  .thm-btn svg{
	 line-height: 1;
	 margin-top: -3px;
  }
  .thm-btn:hover{
	background: black;
  }
  .thm-btn::before{
	content: '';
	position: absolute;
	left: 50%;
	right: 50%;
	top: 0;
	height: 100%;
	width: 0px;
	background: rgba(255, 255, 255, 0.575);
	z-index: 2;
	transition: all 500ms ease-in;
  }
  .thm-btn:hover::before{
	opacity: 0;
	width: 200%;
	left: 0%;
	right: 0%;
  }
  .thm-btn::after{
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	top: 50%;
	bottom: 50%;
	height: 0;
	width: 100%;
	background: rgba(255, 255, 255, 0.575);
	z-index: 2;
	transition: all 500ms ease-in;
  }
  .thm-btn:hover::after{
	opacity: 0;
	height: 200%;
	top: 0;
	bottom: 0;
  }
  .market-btn{
	 box-shadow: 0px 0px 4px black;
	 border: 1px solid white;
  }
  .market-btn::before,
  .market-btn::after{
	 transition: all 340ms ease-in !important;
  }
 /* thm button end */
 
 /* cstm social and contact start */
 .cstm-socials {
	 width: 100%;
	 /* border-left: 1px solid #f2f2f2;
	 border-right: 1px solid #f2f2f2; */
	 padding: 15px 10px;
	 width: 100%;
  }
  .cstm-socials ul {
	 display: flex;
	 flex-wrap: wrap;
	 column-gap: 12px;
	 align-items: center;
	 justify-content: center;
	 list-style: none;
  }
  .cstm-socials ul li a {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 width: 22px;
	 height: 22px;
	 /* border-radius: 50%; */
	 line-height: 1;
	 font-size: 12px;
	 color: gray;
	 /* background: gray; */
	 transition: all 400ms;
  }
  .cstm-socials ul li a svg{
	 width: 70%;
	 height: auto;
	 margin: auto;
  }
  .cstm-socials ul li a:hover {
	 background-color: var(--primry-clr);
	 color: white;
  }
  .cstm-contact-infos {
	 /* border-left: 1px solid #f2f2f2;
	 border-right: 1px solid #f2f2f2; */
	 padding: 15px 30px;
  }
  .cstm-contact-infos ul {
	 display: flex;
	 flex-wrap: wrap;
	 align-items: center;
	 column-gap: 30px;
	 line-height: 1;
	 list-style: none;
  }
  .cstm-contact-infos ul li {
	 display: inline-flex;
	 align-items: center;
	 gap: 15px;
	 font-size: 14px;
	 line-height: 1;
  }
  .cstm-contact-infos ul li svg,
  .cstm-contact-infos ul li i {
	 color: gray;
	 font-size: 22px;
	 line-height: 1;
  }
  .cstm-contact-infos ul li a{
	 font-size: 15px;
	 color: gray;
	 font-weight: 600;
  }
  .cstm-contact-infos ul li a:hover {
	 color: var(--primry-clr);
  }
 
  @media (max-width: 780px) {
	 .cstm-contact-infos{
		padding: 11px 22px;
	 }
	 .cstm-contact-infos ul{
		justify-content: center;
	 }
	 .cstm-contact-infos ul li{
		margin-bottom: 8px;
		text-align: center;
	 }
	 .cstm-socials{
		padding: 8px ;
	 }
  }
 /* cstm social and contact end*/
 
 /* slider buttons start*/
 .slide-btn{
	width: 40px;
	height: 60px;
	background: rgba(255, 255, 255, 0.653);
	position: absolute;
	top: 45%;
	z-index: 5;
	cursor: pointer;
	text-align: center;
	line-height: 60px;
	opacity: 0;
	transition: all 400ms;
 }
 .home-slider:hover .slide-btn{
	opacity: 1;
 }
 .slide-btn:hover{
	background: white;
 }
 .slide-btn span{
	font-size: 18px;
	color: rgb(87, 87, 87);
 }
 .prev-slide{
	left: 0;
 }
 .next-slide{
	right: 0;
 }
 @media (max-width:800px) {
	 .slide-btn{
	  width: 20px;
	  height: 30px;
	  line-height: 30px;
	  top: 40%;
	 }
	 .slide-btn i{
		 font-size: 12px;
		 line-height: 30px;
	 }
 }
 /* slider buttons end */
 
 /* titles start */
 .sm-title{
	font-size: 19px;
	font-weight: 600;
	color: var(--primry-clr);
	line-height: 1.1;
	position: relative;
	display: inline-block;
	padding-bottom: 4px;
 }
 .sm-title::before{
	position: absolute;
	content: "";
	width: 110%;
	height: 2px;
	bottom: 3px;
	left: 55%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	background: transparent;
	-webkit-clip-path: polygon(0 0, 0% 100%, 100% 100%);
	clip-path: polygon(0 0, 0% 100%, 100% 100%);
	/* z-index: -1; */
	background: linear-gradient(-323deg, var(--primry-clr) -68.63%, rgba(252, 88, 40, 0) 105.92%);
 }
 .sect-title{
	font-size: 34px;
	color: black;
	font-weight: 600;
	line-height: 1.10;
 }
 @media (max-width: 600px) {
	 .sect-title{
		 font-size: 26px;
	 }
	 .sm-title{
		 font-size: 13px;
	 }
 }
 /* titles end */
 
 /* animations start */
 .anim-up-down{
	animation: anim-up-down 4.5s ease-in infinite;
 }
 @keyframes anim-up-down {
	0%{transform: translateY(0);}
	50%{transform: translateY(12%);}
	100%{transform: translateY(0);}
 }
 .anim-left-right{
	animation: left-right 4.5s ease-in infinite;
 }
 @keyframes left-right {
	0%{transform: translateX(0);}
	50%{transform: translateX(12%);}
	100%{transform: translateX(0);}
 }
 .anim-scale{
	animation: anim-scale 4s ease-in infinite;
 }
 @keyframes anim-scale {
	0%{transform: scale(1);}
	50%{transform: scale(1.18);}
	100%{transform: scale(1);}
 }
 
 /* animations  end*/
 
